import { Form, Button, Alert } from 'react-bootstrap';
import { useState } from 'react';
//import { Redirect } from 'react-router';

function LoginForm(props) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState('') ;
  
  const handleSubmit = (event) => {
      event.preventDefault();
      setErrorMessage('');
      const credentials = { username, password };
      
      // SOME VALIDATION, ADD MORE!!!
      let valid = true;
      if(username === '' || password === '' || password.length < 6)
          valid = false;
      
      if(valid)
      {
        props.login(credentials);
      }
      else {
        // show a better error message...
        setErrorMessage('Error(s) in the form, please fix it.')
      }
  };

  return (
    <Form>
      {errorMessage ? <Alert variant='danger'>{errorMessage}</Alert> : ''}
      <Form.Group controlId='username'>
          <Form.Label style={{fontSize: "xxx-large", fontFamily: "cursive"}}>Username</Form.Label>
          <Form.Control type='email' value={username} onChange={ev => setUsername(ev.target.value)} />
      </Form.Group>
      <Form.Group controlId='password'>
          <div style={{fontSize: "xxx-large", fontFamily: "cursive"}}>Password</div>
          <Form.Control type='password' value={password} onChange={ev => setPassword(ev.target.value)} />
      </Form.Group>
      <Button style={{height: "80px", width: "280px", fontSize: "xx-large", backgroundColor: "rgb(78, 78, 78)"}} onClick={handleSubmit}>Login</Button>
    </Form>)
}

function LogoutButton(props) {
  return(<>
    <Button style={{fontSize: "x-large"}} variant="outline-black" onClick={props.logout}>Logout</Button>
    <div className='invisible'>  </div>
  </>);
}

export { LoginForm, LogoutButton };